.rakuie-baikyaku {
  margin: 80px 0;
}

.rakuie-baikyaku h2 {
  font-size: 3.6rem;
  line-height: 1.4em;
  letter-spacing: 0.1em;
  text-align: center;
  font-weight: bold;
  position: relative;
  padding-bottom: 20px;
  margin-bottom: 60px;
}

.rakuie-baikyaku h2::after {
  content: '';
  display: block;
  width: 80px;
  height: 4px;
  background: #e45213;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.rakuie-baikyaku ol {
  list-style: none;
}

.rakuie-baikyaku ol li {
  width: 100%;
  background: #fff;
  box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.3);
  position: relative;
}

.rakuie-baikyaku ol li + li {
  margin-top: 40px;
}

.rakuie-baikyaku ol li + li::before {
  content: '';
  display: block;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 10px 20px 0 20px;
  border-color: #e45213 transparent transparent transparent;
  position: absolute;
  top: -15px;
  left: 50%;
  transform: translateX(-50%) translateY(-100%);
}

.rakuie-baikyaku ol li span {
  font-size: 1.3rem;
  color: #fff;
  font-weight: bold;
  text-align: center;
  width: 72px;
  height: 72px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: #e45213;
  font-weight: bold;
  color: #fff;
  position: absolute;
  top: -10px;
  left: -10px;
}

.rakuie-baikyaku ol li span em {
  display: block;
  color: #fff;
  font-size: 2.4rem;
  font-style: normal;
  margin-top: 5px;
}

.rakuie-baikyaku ol li dl {
  position: relative;
  padding: 30px;
}

.rakuie-baikyaku ol li dl dt {
  font-weight: bold;
  text-align: center;
  font-size: 2.4rem;
  color: #70c438;
  margin-bottom: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid #dedede;
  letter-spacing: 0.06em;
}

.rakuie-baikyaku ol li dl dd {
  font-size: 1.6rem;
  line-height: 1.4em;
  text-align: center;
}

@media screen and (max-width: 767px) {
  .rakuie-baikyaku {
    margin: 10.66667vw 0;
  }
  .rakuie-baikyaku h2 {
    font-size: 2.4rem;
    padding-bottom: 4vw;
    margin-bottom: 8vw;
  }
  .rakuie-baikyaku h2::after {
    width: 16vw;
    height: 3px;
  }
  .rakuie-baikyaku ol li {
    box-shadow: 0.8vw 0.8vw 2.66667vw rgba(0, 0, 0, 0.3);
  }
  .rakuie-baikyaku ol li + li {
    margin-top: 8vw;
  }
  .rakuie-baikyaku ol li + li::before {
    border-width: 2.66667vw 5.33333vw 0 5.33333vw;
    top: -2.66667vw;
  }
  .rakuie-baikyaku ol li span {
    font-size: 1.2rem;
    width: 14.66667vw;
    height: 14.66667vw;
    top: -2.66667vw;
    left: -2.66667vw;
  }
  .rakuie-baikyaku ol li span em {
    font-size: 2rem;
    margin-top: 1.06667vw;
  }
  .rakuie-baikyaku ol li dl {
    padding: 5.33333vw;
  }
  .rakuie-baikyaku ol li dl dt {
    font-size: 1.8rem;
    margin-bottom: 4vw;
    padding-bottom: 4vw;
  }
  .rakuie-baikyaku ol li dl dd {
    font-size: 1.4rem;
    text-align: left;
  }
}
